*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.card-container{
    border-radius: 8px;
    font-size: clamp(1rem, 0.95rem + 0.25vw, 1.25rem);
    padding: 5px 5px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border:1px solid rgba(113, 86, 86, 0.395);
    backdrop-filter: blur(10px);
    -webkit-backdrop-filter: blur(10px);
    box-shadow: 5px 5px 15px black;
   
}

.image-container{
    display: flex;
    align-items: center;
    justify-content: center;
}



p{
    text-align: center;
    color: white;
}

@media (max-width: 600px) {
    .card-container{
        width: 79%;
        font-size: 1.2rem;
    }
    .image-container img{
        transform: scale(1.5);
    }
}